<script>
import {mapState, mapGetters} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  data(){
    let vip = {
      "0": {
        name: "精英卡",
        desc: "免费",
        color: "#0FF",
      },
      "1": {
        name: "银卡",
        desc: "100 U / 年",
        color: "#CCC",
      },
      "2": {
        name: "金卡",
        desc: "500 U / 年",
        color: "#FF0",
      },
      "3": {
        name: "白金卡",
        desc: "1000 U / 年",
        color: "#FFF",
      },
      "4": {
        name: "钻石卡",
        desc: "5000 U / 年",
        color: "#0F0",
      },
    };
    let current = {};

    let feature = [{
      icon: "font-o",
      name: "策略模板",
      value: "1 个",
      desc: "可以根据不同的币种制定不同的量化策略",
    },{
      icon: "cash-back-record",
      name: "手动建仓",
      value: "3 个",
      desc: "手动建仓买进选择的币种，系统根据设定的量化策略实时跟踪、自动交易",
    },{
      icon: "after-sale",
      name: "自动建仓",
      value: "1 个",
      desc: "选择系统内置的建仓方案和设定好的量化策略自动建仓、自动交易",
    },{
      icon: "expand-o",
      name: "自定义自动建仓方案",
      value: "1 个",
      desc: "设置不同指标组合，自定义建仓方案，系统根据该方案和设定的量化策略自动建仓、自动交易",
    },];

    return {vip, current, feature};
  },

  computed: {
    bg(){
      return "url(" + require('../assets/vip.png') + ") no-repeat top center";
    },
    level(){
      if (!this.authorized())
        return "尚未登录";
      let l = this.profile.level || 0;
      return this.vip[l]["name"] + "会员";
    },
    intro(){
      if (!this.authorized())
        return "有效期登录后可见";
      return "有效期至 2021-10-31"
    },
  },

  methods: {
    onChange(i) {
      this.current = this.vip[i];
    },
    ...mapState(["profile"]),
    ...mapGetters(["authorized", "uid", "nickname"]),
  }
}
</script>
